import { useState } from 'react'
import './ComfortPlanning.scss'


function ComfortPlanning () {
  const [activeButton, setActiveButton] = useState(0)
  const handleButtonClick = (buttonNumber) => {
    setActiveButton(buttonNumber)
  }
  return (
    <div className='plans wrapper'>
      <div className='plans-info'>
        <h2 className='plans-info__title'>  COMFORT TOWN   - 120   135 .</h2>
        <p className='plans-info__text'>               -  1- .           .</p>
        <div className='plans-info__actions'>
          <button className={activeButton === 0 ? 'plans-info__actions-button main-button' : 'plans-info__actions-button main-button active-plan'} onClick={() => handleButtonClick(0)}>120 </button>
          <button className={activeButton === 1 ? 'plans-info__actions-button main-button' : 'plans-info__actions-button main-button active-plan'} onClick={() => handleButtonClick(1)}>135 </button>
          <button className={activeButton === 2 ? 'plans-info__actions-button main-button' : 'plans-info__actions-button main-button active-plan'} onClick={() => handleButtonClick(2)}>135   </button>
        </div>
      </div>

      <div className='plans-img'>
        <div className={activeButton === 0 ? 'plans-images' : 'plans-images--hidden'}></div>
        <div className={activeButton === 1 ? 'plans-images--kitchen' : 'plans-images--hidden'}></div>
        <div className={activeButton === 2 ? 'plans-images--garage' : 'plans-images--hidden'}></div>
      </div>
    </div>
  )
}

export default ComfortPlanning